const validate =(values)=>{
  var errors ={};
  const { name,detailAddres,phone } = values;
  if(!name){
    errors.name="Vui lng nhp tn ca bn";
  }
  else if(name.trim().length < 1 ){
  errors.name="Vui lng nhp tn ca bn"
  }
   if(!detailAddres ){
    errors.detailAddres="Vui lng nhp a ch ca bn"
    }
   else if(detailAddres.trim().length < 10 ){
      errors.detailAddres="a ch t nht 10 k t"
      }
   if(!phone ){
      errors.phone="Vui lng nhp s in thoi ca bn"
        }
    else if(!Number.isInteger(+phone)){
      errors.phone="S in thoi khng hp l"
     }
    else if(phone.trim().length < 10 ){
      errors.phone="S in thoi khng hp l"
          }
  return errors;

}
export default validate;