import React, { useEffect } from 'react';
import Appgg from '../../components/GoogleMap/GoogleMap';
import MyComponents from '../../components/GoogleMap/GoogleMap';
import './intro.css'

function Introduce(props) {
  useEffect(() => {
    window.scrollTo(0, 0);
  },[])
  return (
    <div className ='wrapIntro'>
    <h1>GII THIU</h1>
    <hr />
    <p>
    Trang gii thiu gip khch hng hiu r hn v ca hng ca bn. Hy cung cp thng tin c th v vic kinh doanh, v ca hng, thng tin lin h. iu ny s gip khch hng cm thy tin tng khi mua hng trn website ca bn.
    </p>
    <ul>
      <li>
      Bn l ai
      </li>
      <li>
      Gi tr kinh doanh ca bn l g
      </li>
      <li>
      a ch ca hng
      </li>
      <li>
      Bn  kinh doanh trong ngnh hng ny bao lu ri
      </li>
      <li>
      Bn kinh doanh ngnh hng online c bao lu
      </li>
      <li>
      i ng ca bn gm nhng ai
      </li>
    </ul>
    </div>
  );
}

export default Introduce;
